body {
  background: linear-gradient(135deg, #f8fafc 0%, #e0e7ff 100%);
  min-height: 100vh;
  font-family: 'Segoe UI', 'Roboto', 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 500px;
  margin: 40px auto;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(60, 72, 100, 0.12);
  padding: 32px 24px 24px 24px;
  text-align: center;
}

h1 {
  color: #4f46e5;
  margin-bottom: 32px;
  font-size: 2.2rem;
  letter-spacing: 1px;
}

h2 {
  color: #6366f1;
  margin-bottom: 18px;
  font-size: 1.3rem;
}

button {
  background: linear-gradient(90deg, #6366f1 0%, #818cf8 100%);
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 10px 22px;
  margin: 8px 6px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.08);
}

button:hover {
  background: linear-gradient(90deg, #818cf8 0%, #6366f1 100%);
  transform: translateY(-2px) scale(1.04);
}

/* Timer display */
.components-Timer-timerDisplay {
  font-size: 2.5rem;
  font-weight: bold;
  color: #4f46e5;
  margin: 18px 0 24px 0;
  letter-spacing: 2px;
}

/* Table styling */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 18px;
  background: #f1f5f9;
  border-radius: 10px;
  overflow: hidden;
}

th, td {
  padding: 10px 8px;
  text-align: center;
}

th {
  background: #6366f1;
  color: #fff;
  font-weight: 600;
}

tbody tr:nth-child(even) {
  background: #e0e7ff;
}

tbody tr:nth-child(odd) {
  background: #f8fafc;
}
